import React from 'react';
import { AlertError } from './alerts/alertError';
import { AlertInfo } from './alerts/alertInfo';
import { AlertSuccess } from './alerts/alertSuccess';
import ComboBox from './comboBox';
import { InputText } from './input/inputText';
import InputMask from 'react-input-mask';

function FormClients(props) {
  const {
    edit,
    clearStates,
    name,
    setName,
    email,
    setEmail,
    phone,
    setPhone,
    segments,
    setSegmentSelect,
    segmentSelect,
    updateClient,
    newClient,
    addUser,
    invalidClient,
    alreadyExist,
  } = props;

  return (
    <div className="modal-content">
      <div className="modal-header">
        {edit ? (
          <h5 className="modal-title">Editar Cliente</h5>
        ) : (
          <h5 className="modal-title">Novo Cliente</h5>
        )}
        {edit && (
          <button
            type="button"
            className="close"
            onClick={clearStates}
            data-dismiss="modal"
            aria-label="Fechar"
            key={0}
          >
            <span aria-hidden="true">&times;</span>
          </button>
        )}
      </div>
      <div className="modal-body">
        <form>
          <label htmlFor="name">Cliente</label>
          <InputText
            type="text"
            className="mb-3"
            id="client"
            value={name}
            onChange={(e) => setName(e.target.value)}
            label='Digite o nome do cliente'
          />
          <label htmlFor="name">E-mail</label>
          <InputText
            type="text"
            className="mb-2"
            id="procedure"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            label='Digite o email'
          />
          <label htmlFor="name">Telefone</label>
          <InputMask
            type="text"
            format="currency"
            className="form-control mb-2 p-3"
            value={phone}
            id="contact"
            mask="(99) 9 9999-9999"
            onChange={(e) => setPhone(e.target.value)}
            placeholder="(__) _ ____ - ____"
            required={true}
          />
          {segments && segments.length > 0 && (
            <div>
              <label htmlFor="name" className="mt-2">
                Segmento
              </label>
              <ComboBox
                title="Selecionar Segmento"
                options={segments.map((item) => item.segment)}
                selectValue={(e, item) => setSegmentSelect(item)}
              />
            </div>
          )}

          {segmentSelect && (
            <div className="text-primary font-weight-bold ml-1 mt-3 mb-2">
              Selecionado:{' '}
              <small className="text-muted h6 mb-3">
                {segmentSelect || 'Nenhum'}
              </small>
            </div>
          )}
          {edit ? (
            <div className='form-row mt-4'>
              <div className='form-group col'>
                <button
                  type="reset"
                  onClick={clearStates}
                  className="btn btn-outline-secondary col"
                  data-dismiss="modal"
                  key={3}
                >
                  Fechar
                </button>
              </div>
              <div className='form-group col'>
                <button
                  type="button"
                  onClick={updateClient}
                  className="btn btn-primary col"
                  key={4}
                >
                  Salvar
                </button>
              </div>
            </div>
          ) : (
            <div className='form-row mt-4'>
              <div className='form-group col'>
                <button
                  type="reset"
                  onClick={clearStates}
                  className="btn btn-outline-secondary col"
                  data-dismiss="modal"
                  key={3}
                >
                  Limpar campos
                </button>
              </div>
              <div className='form-group col'>
                <button
                  type="button"
                  onClick={newClient}
                  className="btn btn-primary col"
                  key={5}
                >
                  Adicionar
                </button>
              </div>
            </div>
          )}
          {addUser === true ? (
            <AlertSuccess title="Cliente adicionado com sucesso." />
          ) : null}
          {addUser === false ? (
            <AlertInfo title="Preencha os campos corretamente." />
          ) : null}
          {invalidClient === true ? (
            <AlertError title="Não foi possível processar a requisição." />
          ) : null}
          {alreadyExist === true ? (
            <AlertInfo title="Esse cliente já existe." />
          ) : null}
        </form>
      </div>
    </div>

  );
}

export { FormClients };
