/* eslint-disable react-hooks/exhaustive-deps */
import { useState } from "react";
import { useEffect } from "react";
import { Navigate } from "react-router-dom";
import { Breadcumb } from "../components/breadcumb";
import { FormClients } from "../components/formClients";
import { authService } from "../services/authService";
import { clientService } from "../services/clientService";
import { segmentService } from "../services/segmentService";
import { HTTP_RESPONSE } from "../utils/constants";

export function CreateClient() {
    const [redirectTo, setRedirectTo] = useState(null);
    const [id_user, setId_user] = useState(null);

    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [phone, setPhone] = useState('');

    const [addUser, setAddUser] = useState(null);

    const [serverError, setServerError] = useState(null);

    const [segments, setSegments] = useState([]);

    const [segmentSelect, setSegmentSelect] = useState(null);

    const [clientAlreadyExist, setClientAlreadyExist] = useState(null);

    let isMounted = true;

    useEffect(() => {
        Promise.all([
            authService.redirectLogin().then((result) => setRedirectTo(result)),
            authService.getUserIdInLocalStorange().then((result) => {
                setId_user(result);
                getSegments(result);
            }),
        ]);

        async function getSegments(id_user) {
            const request = await segmentService.getSegment(id_user);

            if (!request.status) return;

            if (isMounted) setSegments(request.data);
        }
        return () => {
            isMounted = false;
        };
    }, [id_user]);

    if (redirectTo) {
        return <Navigate to={redirectTo} />;
    }

    const clearStates = () => {
        setName('');
        setEmail('');
        setPhone('');
        setSegmentSelect(null);
        const buttonSelector = document.querySelector('#root > div > div.container-main > div:nth-child(3) > div > div.modal-body > form > div:nth-child(7) > div > div > div > div > button');
        if (buttonSelector) buttonSelector.click();
    };

    const newClient = async (event) => {
        event.preventDefault();
        if (!id_user && !phone) {
            setAddUser(true);
            return;
        }

        const segmentSelected = segmentSelect ? segmentSelect : null;

        const request = await clientService.newClient(
            id_user,
            name.trim(),
            email.trim(),
            phone,
            segmentSelected,
        );

        if (HTTP_RESPONSE.SUCCESS.includes(request.status)) {
            setAddUser(true);
            clearStates();
        }

        if (
            request.status === HTTP_RESPONSE.NOT_FOUND ||
            request.status === HTTP_RESPONSE.BAD_REQUEST
        ) {
            setAddUser(false);
            return;
        }

        if (request.status === HTTP_RESPONSE.ERROR || !request.status) {
            setServerError(true);
            return;
        }

        if (request.status === HTTP_RESPONSE.CONFLICT) {
            setClientAlreadyExist(true);
            return;
        }
    };

    if (addUser === false) {
        setTimeout(() => setAddUser(null), 5000);
    }

    if (addUser === true) {
        setTimeout(() => setAddUser(null), 5000);
    }

    if (serverError) {
        setTimeout(() => setServerError(null), 5000);
    }

    if (clientAlreadyExist) {
        setTimeout(() => setClientAlreadyExist(null), 5000);
    }


    return (
        <div className="container-main">
            <Breadcumb page={[{ link: false, name: 'Novo Clientes' }]} />

            <h3 className="title-page">Novo Clientes</h3>

            <div>
                <FormClients
                    clearStates={clearStates}
                    name={name}
                    setName={setName}
                    email={email}
                    setEmail={setEmail}
                    phone={phone}
                    setPhone={setPhone}
                    segments={segments}
                    setSegmentSelect={setSegmentSelect}
                    segmentSelect={segmentSelect}
                    newClient={newClient}
                    addUser={addUser}
                    invalidClient={serverError}
                    alreadyExist={clientAlreadyExist}
                />
            </div>
        </div>
    );
}