/* eslint-disable react-hooks/exhaustive-deps */
import '../css/main.css';

import { WhatsAppService } from '../services/whatsapp-service';
import { authService } from '../services/authService';
import { clientService } from '../services/clientService';
import { segmentService } from '../services/segmentService';

import { CardClients } from '../components/cardClient';
import { FormClients } from '../components/formClients';
import { TopModal } from '../components/topModal';

import { HTTP_RESPONSE } from '../utils/constants';

import React, { useState, useEffect } from 'react';
import { Navigate } from 'react-router-dom';
import InputMask from 'react-input-mask';
import { Alert } from 'react-bootstrap';
import { AlertSuccess } from '../components/alerts/alertSuccess';
import { AlertError } from '../components/alerts/alertError';
import { Breadcumb } from '../components/breadcumb';

export default function Clients() {
  const [redirectTo, setRedirectTo] = useState(null);
  const [id_user, setId_user] = useState(null);

  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [id, setId] = useState(null);

  const [addUser, setAddUser] = useState(null);
  const [clients, setClients] = useState([]);

  const [edit, setEdit] = useState('');
  const [serverError, setServerError] = useState(null);
  const [notFound, setNotFound] = useState(null);

  const [segments, setSegments] = useState([]);

  const [segmentSelect, setSegmentSelect] = useState(null);

  const [deletedClient, setDeletedClient] = useState(null);
  const [clientAlreadyExist, setClientAlreadyExist] = useState(null);

  let isMounted = true;

  useEffect(() => {
    Promise.all([
      authService.redirectLogin().then((result) => setRedirectTo(result)),
      authService.getUserIdInLocalStorange().then((result) => {
        setId_user(result);
        getSegments(result);
      }),
      getClients(),
    ]);

    async function getSegments(id_user) {
      const request = await segmentService.getSegment(id_user);

      if (!request.status) return;

      if (isMounted) setSegments(request.data);
    }
    return () => {
      isMounted = false;
    };
  }, [id_user]);

  async function getClients() {
    const userId = await authService.getUserIdInLocalStorange();
    const request = await clientService.findAllClient(userId);

    if (request.status === HTTP_RESPONSE.ERROR || !request.status) {
      if (isMounted) {
        setServerError(true);
        setClients([]);
      }
      return;
    }

    if (request.status === HTTP_RESPONSE.NOT_FOUND && isMounted) {
      setNotFound(true);
      setClients([]);
      return;
    }

    if (isMounted) setClients(request.data);
  }

  if (redirectTo) {
    return <Navigate to={redirectTo} />;
  }

  const clearStates = () => {
    setEdit(false);
    setName('');
    setEmail('');
    setPhone('');
    setSegmentSelect(null);
  };

  const newClient = async (event) => {
    event.preventDefault();
    if (!id_user && !phone) {
      setAddUser(true);
      return;
    }

    const segmentSelected = segmentSelect ? segmentSelect : null;

    const request = await clientService.newClient(
      id_user,
      name.trim(),
      email.trim(),
      phone,
      segmentSelected,
    );

    if (HTTP_RESPONSE.SUCCESS.includes(request.status)) {
      setAddUser(true);
      clearStates();
    }

    if (
      request.status === HTTP_RESPONSE.NOT_FOUND ||
      request.status === HTTP_RESPONSE.BAD_REQUEST
    ) {
      setAddUser(false);
      return;
    }

    if (request.status === HTTP_RESPONSE.ERROR || !request.status) {
      setServerError(true);
      return;
    }

    if (request.status === HTTP_RESPONSE.CONFLICT) {
      setClientAlreadyExist(true);
      return;
    }

    await getClients();
  };

  const updateClient = async (event) => {
    event.preventDefault();

    const segmentSelected = segmentSelect ? segmentSelect : null;
    const emailField = email ? email.trim() : null;

    const request = await clientService.updateClient(
      id_user,
      id,
      name.trim(),
      emailField,
      phone.trim(),
      segmentSelected,
    );

    if (HTTP_RESPONSE.SUCCESS.includes(request.status)) {
      setAddUser(true);
      clearStates();
      await getClients();
    }

    if (
      request.status === HTTP_RESPONSE.NOT_FOUND ||
      request.status === HTTP_RESPONSE.BAD_REQUEST
    ) {
      setAddUser(false);
    }

    setEdit(false);
    clearStates();
  };

  const deleteClient = async (event, id) => {
    event.preventDefault();
    const request = await clientService.deleteClient(id_user, id);

    if (HTTP_RESPONSE.SUCCESS.includes(request.status)) {
      clearStates();
      setDeletedClient(true);
      await getClients();
    }
  };

  const activeEditClient = async (event, id, name, email, phone, segment) => {
    event.preventDefault();
    setEdit(true);
    setId(id);
    setName(name);
    setEmail(email);
    setPhone(phone);
    setSegmentSelect(segment);
  };

  if (addUser === false) {
    setTimeout(() => setAddUser(null), 5000);
  }

  if (addUser === true) {
    setTimeout(() => setAddUser(null), 5000);
  }

  if (serverError) {
    setTimeout(() => setServerError(null), 5000);
  }

  if (deletedClient) {
    setTimeout(() => setDeletedClient(null), 5000);
  }

  if (clientAlreadyExist) {
    setTimeout(() => setClientAlreadyExist(null), 5000);
  }

  if (notFound) {
    setTimeout(() => setNotFound(null), 5000);
  }

  return (
    <div className="container-main">
      <Breadcumb page={[{ link: false, name: 'Clientes' }]} />

      <h3 className="title-page">Clientes</h3>
      {/* <button
        type="button"
        className="btn btn-outline-primary col p-2"
        data-toggle="modal"
        data-target="#modalClient"
      >
        Adicionar Novo Cliente
      </button> */}

      {/* <AlertSuccess title='Adicionado com sucesso' />
      <AlertError title='Erro ao processar' />
      <AlertInfo title='Nenhuma informação retornada.' /> */}

      <TopModal
        className="btn btn-danger"
        id="delete-client"
        title="Excluir cliente?"
        body="Tem certeza que deseja excluir esse cliente?"
        click={(e) => deleteClient(e, id)}
        button="Excluir"
      />

      <div
        className="modal fade"
        id="modalClient"
        tabIndex="-1"
        role="dialog"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered" role="document">
          <FormClients
            edit={edit}
            clearStates={clearStates}
            name={name}
            setName={setName}
            email={email}
            setEmail={setEmail}
            InputMask={InputMask}
            phone={phone}
            setPhone={setPhone}
            segments={segments}
            setSegmentSelect={setSegmentSelect}
            segmentSelect={segmentSelect}
            updateClient={updateClient}
            newClient={newClient}
            addUser={addUser}
            Alert={Alert}
            invalidClient={serverError}
            alreadyExist={clientAlreadyExist}
          />
        </div>
      </div>
      <h4 className="mt-4 mb-3">Seus clientes</h4>

      <div className="d-inline">
        <strong>Quantidade de clientes: </strong>
        <div className="d-inline">{clients.length}</div>
      </div>

      <div className="mt-2">
        {notFound ? <AlertError title="Nenhum cliente foi retornado." /> : null}

        {serverError === true ? (
          <AlertError title="Não foi possível processar a requisição." />
        ) : null}

        {deletedClient ? (
          <AlertSuccess title="Cliente excluído com sucesso." />
        ) : null}
      </div>

      <CardClients
        clients={clients}
        redirectToWhatsapp={WhatsAppService.redirectToWhatsapp}
        activeEditClient={activeEditClient}
        setId={setId}
      />
    </div>
  );
}
